.weather-secondary {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: auto;
    margin-right: auto;
    padding-left: 55px;
}
.weather-info__first{
    display: flex;
    justify-content: center;
    padding-top: 30px;
}
.weather-info__second{
    display: flex;
    justify-content: center;
    font-weight: 700;
    padding-left: 5px;

}
.weather-info__third{
    display: flex;
    justify-content: center;
}
.weather-info__four{
    display: flex;
    justify-content: center;
    align-items: center;
}
p{
    padding-top: 7px;
}