.search-form {
    width: 100%;
    border-radius: 20px;
    box-shadow: 0px 0px 15px 1px black;
}

.search-form__input {
    width: 80%;
    padding: 10px;
    border: none;
}
.search-form__input:focus{
    outline: none;
}
.search-form__btn{
    width: 20%;
    padding: 10px 0;
    border: none;
}