*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.app{
    height: 100vh;
    background: url('..//images/taylor-van-riper-yQorCngxzwI-unsplash.jpg') center/cover ;
}
.app_container{
    max-width: 300px;
    min-width: 400px;
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 10px;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    width: 100%;
    border: 1px solid #000;
}
.des{
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 20px;
}